<template>
  <div class="item">
    <div class="icon-box" title="运行" @click="handleRun"><img class="icon-image" :src="value.icon" /></div>

    <div class="title-and-description">
      <div class="title" :title="value.path">{{ value.title }}</div>
      <div class="description">{{ value.description }}</div>
    </div>
  </div>
</template>

<script>

// import MyDetail from "./exe-detail.vue";
// import MyOverview from "./exe-overview.vue";

export default {
  name: "home-exe-item",

  // components: { MyDetail, MyOverview },

  methods: {
    handlePopoverShow() { },

    handleRun() {
      let executable = this.value;
      let intent = { executable };
      let vo = {
        intents: [intent],
      };
      this.$store.dispatch("intent/run", vo);
    }
  },

  props: { value: Object },
};
</script>

